import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AllCaseInvestigationsComponent } from './case-investigations/all-case-investigations/all-case-investigations.component';
import { AllCasesRaisedComponent } from './cases-raised/all-cases-raised/all-cases-raised.component';
import { RoutePrivilegeGuard } from 'src/app/erp-procurement/data/services/_AccessControlAuthGuard.service';

const routes: Routes = [
  {
    path: "all-cases-raised",
    component: AllCasesRaisedComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "HumanResourceModule",
      requiredPrivilege: ["Dashboard"],
    },
  },


  {
    path: "all-case-investigations",
    component: AllCaseInvestigationsComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {
      clientName: "HumanResourceModule",
      requiredPrivilege: ["Dashboard"],
    },
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CaseManagementRoutingModule { }
