import { SelectionModel } from "@angular/cdk/collections";
import { HttpParams } from "@angular/common/http";
import { Component, OnInit, ViewChild } from "@angular/core";
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { MatDialog, MatDialogConfig } from "@angular/material/dialog";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { Subject, takeUntil } from "rxjs";
import { NotificationServiceService } from "src/@core/Models/Notification/notification-service.service";
import { TokenStorageService } from "src/app/core/service/token-storage.service";
import { DepartmentsLookupComponent } from "src/app/erp-hr/hr-lookups/configurations-lookups/departments-lookup/departments-lookup.component";
import { SnackbarService } from "src/app/shared/services/snackbar.service";
import Swal from "sweetalert2";
import { InitiateCaseInvestigationComponent } from "../../cases-raised/initiate-case-investigation/initiate-case-investigation.component";
import { CaseInvestigationMngtService } from "src/app/erp-hr/data/case-services/case-investigations.service";
import { Router } from "@angular/router";

@Component({
  selector: "app-all-case-investigations",
  templateUrl: "./all-case-investigations.component.html",
  styleUrls: ["./all-case-investigations.component.scss"],
})
export class AllCaseInvestigationsComponent implements OnInit {
  displayedColumns: string[] = [
    "select",
    "id",
    "departmentName",
    "empPf",
    "fromDate",
    "toDate",
    "employeeName",
    "comments",
    "caseNo",
    "hasCaseToAnswer",
    "status",
    "action",
  ];

  loading: boolean = false;
  data: any;
  error: any;
  dialogConfig: any;
  destroy$: Subject<boolean> = new Subject<boolean>();

  dataSource!: MatTableDataSource<any>;
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  selection = new SelectionModel<any>(true, []);

  downloadLoading: boolean = false;
  posting: boolean = false;
  showForm: boolean = false;

  Form: FormGroup;
  months: string[] = [
    "January",
    "February",
    "March",
    "April",
    "May",
    "June",
    "July",
    "August",
    "September",
    "October",
    "November",
    "December",
  ];

  currentUser: any;

  constructor(
    private dialog: MatDialog,
    private notificationAPI: NotificationServiceService,
    private caseInvestigationMngtService: CaseInvestigationMngtService,
    private snackbar: SnackbarService,
    private fb: FormBuilder,
    private tokenStorageService: TokenStorageService,
    private router: Router
  ) { }

  ngOnInit(): void {
    const currentYear = new Date().getFullYear();
    const currentMonthIndex = new Date().getMonth();
    const currentMonthName = this.months[currentMonthIndex];

    this.currentUser = this.tokenStorageService.getUser();

    this.Form = this.fb.group({
      department: ["All", Validators.required],
      status: ["All"],
    });
    this.Form.valueChanges.subscribe(() => {
      this.getData();
    });

    this.getData();
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }

  //************************************************************************************************ */
  reqStatuses: any[] = [
    { name: "All" },
    { name: "Pending" },
    { name: "Approved" },
    { name: "Rejected" },
    { name: "Investigation" },
    { name: "Has-a-case" },
    { name: "Disciplinary-Hearing" },
    { name: "Show-cause-issued" },
    { name: "Show-cause-responded" },
    { name: "Completed" },
  ];
  selectedStatus: string = "All";

  getSelectSelectedStatus() {
   // this.getData();
  }
  getSelectedStatus(status: any) {
    this.Form.patchValue({
      status: status,
    });

  //  this.getData();
  }
  getData() {
    this.loading = true;
    this.dataSource = new MatTableDataSource([]);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;

    this.selectedStatus = this.Form.value.status;

    let department = this.Form.value.department;

    if (department === "All") {
      department = "%";
    }

    let params = new HttpParams()
      .set("departmentId", department)
      .set("status", this.Form.value.status);
    this.caseInvestigationMngtService
      .read(params)

      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          if (res.statusCode === 302) {
            this.data = res.entity;
            console.log("Case Investigations displayed on table", this.data);

            this.loading = false;
            this.dataSource = new MatTableDataSource(this.data);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
          } else {
            this.loading = false;
          }
        },
        error: (err) => {
          this.loading = false;
          this.notificationAPI.alertWarning("Server Error:  " + err.message);
        },
        complete: () => { },
      });
  }
  refresh() {
    this.getData();
  }
  //********************************************************************************************************** */
  mngRecord(action: any, record?: any) {
    let route = "/erp-hr/case-management/all-cases-raised";
    this.router.navigate([route], {
      queryParams: {
        action: action,
      },
    });

  }
  onInvestigation(record?: any, action?: any,) {
    console.log("this.action:; ", action);
    console.log("this.record:; ", record);

    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.disableClose = true;
    dialogConfig.width = "800px";
    dialogConfig.data = {
      action: action,
      record: record,
    };
    const dialogRef = this.dialog.open(
      InitiateCaseInvestigationComponent,
      dialogConfig
    );
    dialogRef.afterClosed().subscribe((result) => {
      console.log("result:::", result);

      this.getData();
    });
  }

  //************************************************************************************************* */
  deleteRecord(id: number) {
    Swal.fire({
      title: "Are you sure?",
      text: "RECORD WILL BE DELETED PERMANENTLY!!",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!",
    }).then((result) => {
      if (result.isConfirmed) {
        this.loading = true;
        this.caseInvestigationMngtService
          .delete(id)
          .pipe(takeUntil(this.destroy$))
          .subscribe({
            next: (res) => {
              if (res.statusCode == 200) {
                this.getData();
                this.loading = false;
                this.notificationAPI.alertSuccess(
                  "RECORD DELETED SUCCESSFULLY"
                );
              } else {
                this.getData();
                this.loading = false;
                this.notificationAPI.alertWarning(res.message);
              }
            },
            error: (err) => {
              this.loading = false;
              this.notificationAPI.alertSuccess("Server Error: " + err.message);
            },
            complete: () => { },
          });
      }
    });
  }

  input: any;
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.input = filterValue;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }
  //******************************************************************************************************************************** */
  departmentLookup(type: any) {
    if (type === "Search") {
      const dialogConfig = new MatDialogConfig();
      dialogConfig.disableClose = false;
      dialogConfig.disableClose = true;
      dialogConfig.width = "800px";
      dialogConfig.data = {
        action: "SingleCostCenter",
      };
      const dialogRef = this.dialog.open(
        DepartmentsLookupComponent,
        dialogConfig
      );
      dialogRef.afterClosed().subscribe((res) => {
        if (res && res.data && res.data.length > 0) {
          this.Form.patchValue({
            department: res.data[0].id,
          });
        }
      });
    } else if (type === "All") {
      this.Form.patchValue({
        department: "All",
      });

      this.getData();
    }
  }
  // ****************************************************************************************************************************
  selectedRows: any[] = [];
  atleastOneSelected: boolean = false;
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }
  masterToggle() {
    if (this.isAllSelected()) {
      this.selection.clear();
      return;
    }

    this.selection.select(...this.dataSource.data);
  }
  checkboxLabel(row?: any): string {
    if (!row) {
      return `${this.isAllSelected() ? "deselect" : "select"} all`;
    }
    return `${this.selection.isSelected(row) ? "deselect" : "select"} row ${row.position + 1
      }`;
  }

  expSelected() {
    this.atleastOneSelected = true;
    this.selectedRows = this.selection.selected;

    console.log("this.selectedRows: ", this.selectedRows);
  }

  hasAccess: boolean = true;

  processMultipleRows(status: any) {
    Swal.fire({
      title: "Verify",
      confirmButtonText: "Submit",
      showCancelButton: true,
      cancelButtonText: "Cancel",
      input: "textarea",
      inputPlaceholder: "Type your remarks here...",
      inputAttributes: {
        class: "form-control",
        rows: "4",
      },
      customClass: {
        container: "swal-container",
        confirmButton: "swal-confirm-button",
        cancelButton: "swal-cancel-button",
        title: "swal-title",
        input: "swal-input",
      },
      inputValidator: (value) => {
        if (!value) {
          return "Please enter your remarks";
        } else {
          if (this.hasAccess) {
            const processedRows = this.selection.selected.map((element) => {
              return {
                id: element.id,
                status: status,
                verifierRemarks: value,
              };
            });

            console.log("processedRows:: ", processedRows);
            this.loading = true;
            this.caseInvestigationMngtService
              .verify(processedRows)
              .pipe(takeUntil(this.destroy$))
              .subscribe({
                next: (res) => {
                  if (this.isSuccessfulResponse(res.statusCode)) {
                    this.notificationAPI.alertSuccess(res.message);
                  } else {
                    this.notificationAPI.alertWarning(res.message);
                  }
                },
                error: (err) => {
                  this.notificationAPI.alertWarning(err.message);
                },
                complete: () => {
                  this.getData();
                  this.clearSelection();
                  this.loading = false;
                },
              });
          }
          return null;
        }
      },
    });
  }

  isSuccessfulResponse(statusCode: number): boolean {
    return [201, 200, 302].includes(statusCode);
  }

  clearSelection() {
    this.selection = new SelectionModel<any>(true, []);
    this.selection.clear();
  }
}
